<!--  -->
<template>
  <div>
    <div
      class="search-bar"
      :class="{ 'hide-title': !searchBarVisible, 'hide-shadow': shadowVisible }"
    >
      <transition name="search-move">
        <div class="search-bar-title-wrapper" v-show="searchBarVisible">
          <div class="title-text-wrapper">
            <span class="title-text title">{{ $t('home.title') }}</span>
          </div>
          <div class="title-icon-shake-wrapper">
            <span class="icon-shake icon" @click="showFlapCard"></span>
          </div>
        </div>
      </transition>
      <div
        class="title-icon-back-wrapper"
        :class="{ 'hide-title': !searchBarVisible }"
        @click="back"
      >
        <span class="icon-back icon"></span>
      </div>

      <div
        class="search-bar-input-wrapper"
        :class="{ 'hide-title': !searchBarVisible }"
      >
        <div
          class="search-bar-blank"
          :class="{ 'hide-title': !searchBarVisible }"
          @click="hideHotSearch"
        ></div>
        <div class="search-bar-input">
          <span class="icon-search icon"></span>
          <input
            class="input"
            type="text"
            :placeholder="$t('home.hint')"
            v-model="searchText"
            @click="showHotSearch"
            @keyup.13.exact="searchByKey"
          />
        </div>
      </div>
    </div>
    <HotSearchList v-show="hotSearchVisible" ref="hotSearch" />
    <FlapCard :data="random" />
  </div>
</template>

<script>
import { homeSearchMixin } from '../../utils/mixin'
import HotSearchList from './HotSearchList'
import FlapCard from './FlapCard'
import { home } from '../../api/store'
export default {
  mixins: [homeSearchMixin],
  components: {
    HotSearchList,
    FlapCard,
  },
  data() {
    return {
      searchText: '',
      searchBarVisible: true,
      shadowVisible: false,
      hotSearchVisible: false,
      random: {},
    }
  },
  watch: {
    offsetY(v) {
      if (v > 0) {
        this.hideBar()
        this.showShadow()
      } else {
        this.showBar()
        this.hideShadow()
      }
    },
    hotSearchOffsetY(offsetY) {
      if (offsetY > 0) {
        this.showShadow()
      } else {
        this.hideShadow()
      }
    },
  },
  methods: {
    back() {
      this.$router.go(-1)
    },
    searchByKey() {
      this.$router.push({
        path: '/store/list',
        query: {
          keyword: this.searchText,
        },
      })
    },
    showFlapCard() {
      this.setFlapCardVisible(true)
    },
    hideHotSearch() {
      // console.log('click')
      this.hotSearchVisible = false
      this.showBar()
      if (this.offsetY > 0) {
        this.showShadow()
        this.hideBar()
      } else {
        this.showBar()
        this.hideShadow()
      }
    },
    showHotSearch() {
      this.hotSearchVisible = true
      this.hideShadow()
      this.hideBar()
      this.$nextTick(() => {
        this.$refs.hotSearch.reset()
      })
      // if (this.hotSearchOffsetY > 0) {
      //   this.showShadow()
      // } else {
      //   this.hideHotSearch()
      // }
    },
    hideBar() {
      this.searchBarVisible = false
    },
    showBar() {
      this.searchBarVisible = true
    },
    hideShadow() {
      this.shadowVisible = false
    },
    showShadow() {
      this.shadowVisible = true
    },
  },
  mounted() {
    home().then((res) => {
      if (res && res.status === 200) {
        const data = res.data
        const random = Math.floor(Math.random() * data.random.length)
        this.random = data.random[random]
        // console.log(this.random)
      }
    })
  },
}
</script>
